import React from "react"
import { Link } from 'gatsby'

export default props => (
    <div style={{
        margin: `3rem auto`,
        maxWidth: 450
    }}>
        <h3>You are at {props.location}</h3>
        <p style={{textAlign: `justify`}}>  
            Welcome to my personal site, 
            this is currently in development stage, 
            made with <span role="img" aria-label="heart">❤️</span> and Gatsby.js!
            <br/>
            <strong>Sarah Faith</strong>
        </p>
        <div style={{ display: `flex`, justifyContent: `space-around`, maxWidth: 450}}> 
            <Link to='/'>Home</Link>
            <Link to='/blogs/'>Blogs</Link>
            <Link to='/about/'>About</Link>
        </div>
     </div>
)