import React from 'react'
import styles from './article-front.module.css'

export default props => (
    <div className={styles.article__box}>
        <article className={styles.article__front}>
            <h2 className={styles.article__title}>{props.title}</h2>
            <small>{props.date} • 4 min read</small>
        </article>
    </div>
)